import {ActionHandlerManager, StateActionHandler, ActionHandler} from '../utils/actions'
import {ShowDialog, SelectedTab} from '../actions/mainActions'
import {ShowDialogHandler} from './actionHandlers'

const handlerManager = new ActionHandlerManager();

const initState = {
  selectedTab: 'trades',
};

export default function homeReducer(state = initState, action) {
  return handlerManager.dispatch(state, action);
}


handlerManager.addHandler(new ShowDialogHandler(ShowDialog));

handlerManager.addHandler(new ActionHandler(SelectedTab, {
  handle(state, action){
    let newState = Object.assign({}, state);
    newState.selectedTab = action.tab;
    return newState;
  }
}));


