import {ActionHandlerManager, StateActionHandler, ActionHandler} from '../utils/actions'
import {GetPointDetailed,GetAllLines} from '../actions/huashanAction'
import {ShowDialogHandler} from './actionHandlers'
import loadingProgress from '../utils/loadingProgress'

const handlerManager = new ActionHandlerManager();

const initState = {
  pointDetialed:null,
  RecommendLine:null
};
export default function huashanReducer(state = initState, action) {
  return handlerManager.dispatch(state, action);
}

/**
 * 
 */
handlerManager.addHandler(new StateActionHandler(GetPointDetailed,{
  onStart(state, action){
    let ns =Object.assign({},state);
    return ns;
  },
  onSucceed(state, action){
    let ns =Object.assign({},state,action);
    return ns;
  },
  onError(state,action){
    let ns =Object.assign({},state);
    return ns;
  }
}))

/**
 * 
 */
handlerManager.addHandler(new StateActionHandler(GetAllLines,{
  onStart(state, action){
    let ns =Object.assign({},state);
    return ns;
  },
  onSucceed(state, action){
    let ns =Object.assign({},state,action);
    return ns;
  },
  onError(state,action){
    let ns =Object.assign({},state);
    return ns;
  }
}))



