import React, {Component, PropTypes} from "react";
import ReactDOM from 'react-dom'
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import s from "./TradeEdit.less";
import * as _tradesActions from "../../actions/tradesActions";
// import * as _typeAction from "../../actions/typesActions";
import {navigate} from "../../actions/routeActions";
import {STATIC_IMG_URL} from '../../config'
import NumberInput from '../../components/NumberInput'
import Numberkeyboard from '../../components/NumberKeybord'
import {IOType, TallyChannels, TypeDefaultImage} from '../../constants'
import {ListLabelValueItem} from '../../components/ListItem'
import RelativeModule from '../../components/RelativeModule'
import {formatToyyyyMMdd} from '../../utils/timeUtils'


class TradeEdit extends Component {

  static contextTypes = {
    setTitle: PropTypes.func.isRequired,
  };
  static propTypes = {
    tradeId: PropTypes.number.isRequired, // edit|add
  };
  static defaultProp = {};

  componentWillMount() {
    if (!this.props.trade) {
      if (this.props.tradeId == 0) {
        this.props.tradesActions.editTrade({tallyId: 0}, false);
      } else {
        //
        this.props.tradesActions.getTradeDetail(this.props.tradeId)
          .then(({trade})=> {
            if (trade) {
              this.props.tradesActions.editTrade(trade, false);
            }
          });
      }
    }
  }

  render() {
    this.context.setTitle(this.props.tradeId > 0 ? '' : '');
    let {trade} = this.props;
    if (!trade) {
      //TODO 
      return <div></div>
    }
    return (
      <div className={s.root}>
        <div className={s.content}>
          <div className={s.input}>
            <div className={s.left} onClick={()=> {
              this.refs.amountInput.senKey(12)
            }}>
              <div className={s.label}></div>
              <img className={s.icon} src={STATIC_IMG_URL + '/edit_income.png'}/>
            </div>
            <div className={s.right}>
              <div className={s.typeDesc}></div>
              <NumberInput ref="amountInput" className={s.amount}/>
              <div className={s.unit}></div>
            </div>
          </div>
        </div>
        <div className={s.keyBoard}>
          <Numberkeyboard />
        </div>
      </div>
    )
  }
}


const ConnectedComp = connect(
  (store, ownProps) => {
    let trade = store.trades.details[ownProps.tradeId] || {tallyId: ownProps.tradeId};
    return {
      trade: store.trades.editingTrade,
    }
  },
  dispatch =>({
    tradesActions: bindActionCreators(_tradesActions, dispatch),
    navigate: bindActionCreators(navigate, dispatch),
  })
)(TradeEdit);

export default ConnectedComp;


