import { } from '../constants';
import {dispatchAction, showDialog as _showDialog} from './actions'
import {ActionType} from '../utils/actions'

export const ModuleName = 'Main';
export const ShowDialog = new ActionType('ShowDialog',ModuleName);
export const SelectedTab = new ActionType('SelectedTab', ModuleName);


export function showDialog(dialog) {
  return _showDialog(ShowDialog.actionName, dialog);
}

export function selectedTab(tab) {
  return (dispatch) => {
    dispatch(SelectedTab.newAction({tab}));
  }
}



