import React, {Component, PropTypes} from "react";
import {connect} from "react-redux";
import {navigate} from "../../actions/routeActions";
import {bindActionCreators} from "redux";
import {STATIC_IMG_URL} from '../../config'
import s from "./EmailCreditCardDetials.css"
import * as _emailAction from "../../actions/emailAction";

class EmailCreditCardDetials extends Component{
  componentDidMount() {
    this.props.emailAction.getCreditCardDetials(this.props.year,this.props.month);
  }
  render(){
    var table=[];
    if(this.props.creditCardDetail != null){
      if(this.props.creditCardDetail.creditCardTallyList != undefined){
        table=this.props.creditCardDetail.creditCardTallyList.map(function (data,index) {
          return(
            <BottomTableCell
              key={index}
              billData={data}
            />
          )
        }.bind(this))
      }
    }
    return(
      <div>
        <div className={s.topGray}/>
        <TopView
          iconUrl={this.props.creditCardDetail?this.props.creditCardDetail.bankIcon?this.props.creditCardDetail.bankIcon:'':''}
          bankName={this.props.creditCardDetail?this.props.creditCardDetail.bankName?this.props.creditCardDetail.bankName:'-':'-'}
          tailNumber={this.props.creditCardDetail?this.props.creditCardDetail.tailNumber?this.props.creditCardDetail.tailNumber:'-':'-'}
          cardUserName={this.props.creditCardDetail?this.props.creditCardDetail.cardUserName?this.props.creditCardDetail.cardUserName:'-':'-'}
        />
        <MiddleView
          year={this.props.year}
          month={this.props.month}
          newBalance={this.props.creditCardDetail?this.props.creditCardDetail.newBalance?this.props.creditCardDetail.newBalance:'-':'-'}
          minPayment={this.props.creditCardDetail?this.props.creditCardDetail.minPayment?this.props.creditCardDetail.minPayment:'-':'-'}
          billDateMills={this.props.creditCardDetail?this.props.creditCardDetail.billDateMills?this.props.creditCardDetail.billDateMills:'-':'-'}
          paymentDueDateMills={this.props.creditCardDetail?this.props.creditCardDetail.paymentDueDateMills?this.props.creditCardDetail.paymentDueDateMills:'-':'-'}
        />
        <div className={s.topGray}/>
        {table}
        <hr/>
      </div>
    )
  }
}

class TopView extends  Component{
  render(){
    return(
      <div className={s.topView}>
        <img className={s.topIcon} src={STATIC_IMG_URL+"/index_ic_today.png"}/>
        <div className={s.topMiddle}>
          <div className={s.topBankName}>{this.props.bankName}</div>
          <div className={s.topCardText}>
            <div >{this.props.tailNumber}</div>
            <div className={s.topCardName}>{this.props.cardUserName}</div>
          </div>
        </div>
      </div>
    )
  }
}

class MiddleView extends Component{
  render(){
    this.windowWidth=window.innerWidth;
    let rightStyle={
      "width":(this.windowWidth-160)+"px",
    }
    return(
      <div className={s.middleView}>
        <div className={s.dateView}>
          <div>
            <div className={s.middleYear}>{this.props.year}</div>
            <div className={s.middleMonth}>
              <div style={{"fontSize":"70px"}}>{this.props.month}</div>
              <div style={{"fontSize":"24px","paddingBottom":"14px"}}></div>
            </div>
          </div>
          <img className={s.iconChooseDate} src={STATIC_IMG_URL+"/mail_choose_date.png"}/>
        </div>
        <div className={s.middleRightAllView}>
          <div className={s.middleRightTopView}>
            <div className={s.middleRightPartView}>
              <div className={s.creditText1}>()</div>
              <div className={s.creditText2}>{this.props.newBalance}</div>
            </div>
            <div className={s.middleRightPartView}>
              <div className={s.creditText1}>()</div>
              <div className={s.creditText2}>{this.props.minPayment}</div>
            </div>
          </div>
          <div className={s.middleRightTopView}>
            <div className={s.middleRightPartView}>
              <div className={s.creditText1}></div>
              <div className={s.creditText2}>{this.props.billDateMills}</div>
            </div>
            <div className={s.middleRightPartView}>
              <div className={s.creditText1}></div>
              <div className={s.creditText2}>{this.props.paymentDueDateMills}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

class BottomTableCell extends Component{
  render(){
    return(
      <div >
        <div className={s.botttomCell}>
          <div className={s.cellDate}>{this.props.billData.transDateMillis}</div>
          <div className={s.cellRemark}>{this.props.billData.description}</div>
          <div className={s.cellDate}>{this.props.billData.amountMoney}</div>
        </div>
        <hr className={s.cellline}/>
      </div>
    )
  }
}

const ConnectedComp = connect(
  store => ({
    creditCardDetail:store.email.creditCardDetail,
    year:store.email.year,
    month:store.email.month,
  }),
  dispatch =>({
    emailAction:bindActionCreators(_emailAction, dispatch),
    navigate: bindActionCreators(navigate, dispatch),
  })
)(EmailCreditCardDetials);

export default ConnectedComp;



