/**
 * example of mockData
 * mockData = {
 * key:'urlPath',
 * delay:1000,
 * response:{},
 * request:{},
 * responseData: {},
 * error:{resultCode:1,resultMsg:''}
 * };
 */
class MockManager {
  constructor() {
    this.mockData = [
      require('./tallyTypeList.mock.json'),
      require('./tallyListPage.mock.json'),
      require('./tallyListPage2.mock.json'),
      require('./tallySelect.mock.json'),
      require('./emailList.mock.json'),
      require('./deleteEmail.mock.json'),
      require('./emailQueryCreditCardList.mock.json'),
      require('./emailreditCardBillDetail.mock.json'),
      ...require('./emailLoginIn.mock.json'),
      require('./huashanDetialed.mock.json'),
      require('./huashanGetAllLines.mock.json'),
    ];
  }

  getMockData(key) {
    if (this.mockData.length == 0) {
      return null;
    }
    for (let data of this.mockData) {
      if (data.hasOwnProperty('key') && data.key == key) {
        return data;
      }
    }
  }
}

const mockManager = new MockManager();
export {
  mockManager
}




