import React, {Component, PropTypes} from "react";
import ReactDOM from 'react-dom';
import {connect} from "react-redux";
import s from "./TradesTab.less";
import * as _mainActions from "../../actions/mainActions";
import {navigate} from "../../actions/routeActions";
import {bindActionCreators} from "redux";
import {STATIC_IMG_URL} from '../../config'
import {Carousel} from 'antd-mobile'
import * as _tradesActions from '../../actions/tradesActions'
import {isSameDay, dateFromStr} from '../../utils/timeUtils'
import Amount from '../../components/Amount'

class TradesTab extends Component {

  componentDidMount() {
    let date = new Date();
    this.props.tradesActions.getTradeList(date.getFullYear(), date.getMonth()+1, 1, 10);
  }

  onListScroll = ()=> {
    let list = this.refs.list;
    let listDom = ReactDOM.findDOMNode(list);
    if(listDom.scrollHeight <= listDom.scrollTop+listDom.clientHeight){
      //
      if(this.props.loadingState == 'loading' || this.props.loadingState == 'over'){
        return;
      }
      setTimeout(()=>{
        this.props.tradesActions.getTradeList(this.props.year, this.props.month, this.props.pageIndex+1, 10);
      }, 500);
    }
  };

  render() {
    const tradesComp = (trades) => {
      let ret = [];
      if (!trades || trades.length == 0) {
        //TODO 
        return null;
      }
      let lastTime = null;
      for (let i = 0; i < trades.length; i++) {
        let timeDesc = '';
        let trade = trades[i];
        let time = dateFromStr(trade.time);
        if (!isSameDay(lastTime, time)) {
          timeDesc = time.getDate() + '';
        }
        lastTime = time;
        ret.push(<TradeItem key={trade.tallyId}
                            onClick={() => {this.props.navigate(`/trades/${trade.tallyId}`)}}
                            trade={trade}
                            tallyType={this.props.getTypeById(trade.typeOpcodes)}
                            timeDesc={timeDesc}/>)
      }
      return ret;
    };

    return (
      <div className={s.root}>
        <div className={s.header}>
          <div className={s.headerDate}>
            <div className={s.headerDateYear}>{this.props.year}</div>
            <div className={s.headerDateMonth}>
              <span className={s.headerDateMonthValue}>{this.props.month}</span>
              <span className={s.headerDateMonthLabel}></span>
            </div>
          </div>
          <div className={s.headerInfo}>
            <Carousel infinite={true}>
              <div className={s.headerInforItem}>
                <div className={s.itemColumn}>
                  <div className={s.itemColumnLabel}></div>
                  <div
                    className={s.itemColumnValue}>{this.props.monthIncome ? this.props.monthIncome.toFixed(2) : "__"}</div>
                </div>
                <div className={s.itemColumn}>
                  <div className={s.itemColumnLabel}></div>
                  <div
                    className={s.itemColumnValue}>{this.props.monthExpense ? this.props.monthExpense.toFixed(2) : "__"}</div>
                </div>
              </div>
              <div className={s.headerInforItem}>
                <div className={s.itemColumn}>
                  <div className={s.itemColumnLabel}></div>
                  <div
                    className={s.itemColumnValue}>{this.props.monthSurplus ? this.props.monthSurplus.toFixed(2) : "__"}</div>
                </div>
                <div className={s.itemColumn}>
                  <div className={s.itemColumnLabel}></div>
                  <div
                    className={s.itemColumnValue}>{this.props.monthInvest ? this.props.monthInvest.toFixed(2) : '__'}</div>
                </div>
              </div>
            </Carousel>
          </div>
        </div>

        <div ref="list" className={s.list} onScroll={this.onListScroll}>
          {tradesComp(this.props.trades)}
          {this.props.loadingState == 'loading' ? <TradeLoadingItem /> : null}
          <TradesFooter ref="footer"/>
        </div>
        <img className={s.headerArrowDown} src={STATIC_IMG_URL + "/tally_header_down_arrow.png"}/>
      </div>
    )
  }
}

class TradeItem extends Component {
  render() {
    const {timeDesc, trade, tallyType} = this.props;
    return (
      <div className={s.tradeItem} onClick={this.props.onClick}>
        <div className={s.date}>{timeDesc}</div>
        <div className={s.iconContainer}>
          <i className={s.line}/>
          <img className={s.icon} src={tallyType.iconDisplay || STATIC_IMG_URL + '/ic_type_custom_sel.png'}/>
        </div>
        <div className={s.info}>
          <div className={s.title}>
            <div className={s.name}>{tallyType.typeDesc}</div>
            <div className={s.amount}><Amount ioType={trade.ioType} amount={trade.amount}/></div>
          </div>
          {trade.photoUrl && trade.photoUrl.length > 0 || trade.remark && trade.remark.trim().length > 0
            ? <div className={s.detail}>
            {trade.photoUrl && trade.photoUrl.length > 0
              ? <img className={s.detailPicture} src={STATIC_IMG_URL + '/trade_item_ic_picture.png'}/>
              : null}
            {trade.remark && trade.remark.trim().length > 0
              ? <div
              className={s.detailContent + ' ' + (trade.photoUrl && trade.photoUrl.length > 0 ? s.detailContentWithPic : '')}>{trade.remark}</div>
              : null}
          </div>
            : null}

        </div>
      </div>
    )
  }
}

class TradeLoadingItem extends Component {
  render() {
    return (
      <div className={s.loadingItem}>
        <div className={s.loadingAnim}>
          <i className={s.line}/>
          <img className={s.icon} src={STATIC_IMG_URL + "/tally_item_ic_loading.png"}/>
        </div>
        <div className={s.message}>...</div>
      </div>);
  }
}

class TradesFooter extends Component {
  render() {
    return (
      <div className={s.footer}>
        <div className={s.iconContainer}>
          <i className={s.line}/>
          <img className={s.icon} src={STATIC_IMG_URL + "/tally_item_ic_footer.png"}/>
        </div>
        <div className={s.message}></div>
      </div>);
  }
}

const ConnectedComp = connect(
  store => ({
    loadingState: store.trades.loadingState,
    pageIndex:store.trades.pageIndex,
    trades: store.trades.trades,
    getTypeById: store.types.getTypeById.bind(store.types),
    year: store.trades.year,
    month: store.trades.month,
    monthIncome: store.trades.monthIncome,
    monthExpense: store.trades.monthExpense,
    monthSurplus: store.trades.monthSurplus,
    monthInvest: store.trades.monthInvest,
  }),
  dispatch =>({
    tradesActions: bindActionCreators(_tradesActions, dispatch),
    navigate: bindActionCreators(navigate, dispatch),
  })
)(TradesTab);

export default ConnectedComp;



