import {StateActionType} from '../utils/actions'
import * as typesAtions from '../actions/typesActions'

export const ModuleName = 'App';
export const InitAPP = new StateActionType('Init_APP',ModuleName);

export function initApp() {
  return(dispatch)=>{
    dispatch(InitAPP.newStartAction());
    dispatch(InitAPP.newSucceedAction())
  }
}





