import React, {Component, PropTypes} from "react";
import ReactDOM from "react-dom";
import s from "./NumberKeyboard.less";
// import * as _typeAction from "../../actions/typesActions";

class NumberInput extends Component {

  constructor() {
    super();

    this.state = {
      plus: false,
    }
  }

  render() {


    return (
      <div ref="root" className={s.root + (this.props.className ? ' ' + this.props.className : '')}>
        <div className={s.col}>
          <div className={s.row}>1</div>
          <div className={s.row}>4</div>
          <div className={s.row}>7</div>
          <div className={s.row}>.</div>
        </div>
        <div className={s.col}>
          <div className={s.row}>2</div>
          <div className={s.row}>5</div>
          <div className={s.row}>8</div>
          <div className={s.row}>0</div>
        </div>
        <div className={s.col}>
          <div className={s.row}>3</div>
          <div className={s.row}>6</div>
          <div className={s.row}>9</div>
          <div className={s.row}>C</div>
        </div>
        <div className={s.col}>
          <div className={s.row}>B</div>
          <div className={s.row}>+</div>
          <div className={s.rowS2}>S</div>
        </div>
      </div>)
  }
}


export default NumberInput;


