import {ActionHandlerManager, ActionHandler, StateActionHandler} from '../utils/actions'
import {InitAPP} from '../actions/appActions'


const handlerManager = new ActionHandlerManager();

const initState = {
  initState:'idle', //idle|doing|done|error
  initError:null,
};

export default function homeReducer(state = initState, action) {
  return handlerManager.dispatch(state, action);
}

handlerManager.addHandler(new StateActionHandler(InitAPP, {
  onStart(state, action){
    return Object.assign({}, state, {initState:'doing'});
  },
  onSucceed(state, action){
    return Object.assign({}, state, {initState:'done'});
  },
  onError(state, action){
    return Object.assign({}, state, {initState:'error', initError:action.message});
  }

}));


