import React, {Component, PropTypes} from "react";
import {connect} from "react-redux";
import * as _huashanAction from "../../actions/huashanAction";
import {navigate} from "../../actions/routeActions";
import {bindActionCreators} from "redux";
import s from "./ChooseLines.css"
import {STATIC_IMG_URL} from '../../config'
import { Modal,WhiteSpace, WingBlank, Flex,Tabs } from 'antd-mobile';
import TopTab from './TopTab'

const TabPane = Tabs.TabPane;

class ChooseLines extends Component{
  componentWillMount() {
    this.props.huashanAction.getAllLines();
  }
  constructor() {
    super();
    this.state = {selectNum: 0}
  }
  testSelectedNum(index) {
    this.state.selectNum = index;
    this.setState({
      selectNum:this.state.selectNum
    });
  }
  beforeSlide(from, to) {
    this.testSelectedNum(to);
  }
  goToDetails(id){
    this.props.navigate(`/huashan/ScenicDetail/${id}`)
  }
  render(){
    var testOption={
      'nameArray':[
      ],
      'selectNum':this.state.selectNum
    }
    const settings = {
      dots: false,
      infinite: false,
      selectedIndex: this.state.selectNum,
      beforeChange: this.beforeSlide.bind(this),
    };
    var scroll=[];
    let mainView;
    if(this.props.RecommendLine != undefined){
      scroll=this.props.RecommendLine.map(function (data,index) {
        testOption.nameArray.push({"name":data.shortName})
        return(
        <TabPane
          key={index}
        >
          <MailView
            scrollData={data}
            goToDetails={(id) =>{this.goToDetails(id)}}
          />
        </TabPane>
        )
      }.bind(this))
      mainView =
        <Tabs
          activeKey={this.state.selectNum.toString()}
          onChange={this.testSelectedNum.bind(this)}
        >
        {scroll}
      </Tabs>
    }
    return(
      <div className={s.mainBack}>
        <TopTab option={testOption} selected={this.testSelectedNum.bind(this)}/>
        {mainView}
      </div>
    )
  }
}

class MailView extends Component{
  render(){
    var table=[];
    if(this.props.scrollData.pointInfoList != undefined){
      table=this.props.scrollData.pointInfoList .map(function (data,index) {
        return(
          <Cell
            key={index}
            data={data}
            goToDetails={ (id) =>{this.props.goToDetails(id)}}
          />
        )
      }.bind(this))
    }
    return(
      <div>
        <MiddleView
          data ={this.props.scrollData}
        />
        {table}
      </div>
    )
  }
}

class MiddleView extends Component{
  render(){
    let startView=[];
    for(let i=0;i<this.props.data.powerTime;i++){
      let star = <div className={s.starView} key={i}><img className={s.star} src={STATIC_IMG_URL + "/cell_star.png"}/></div>
      startView.push(star);
    }
    return(
      <div className={s.middleView}>
        <img className={s.middleBackImage} src={STATIC_IMG_URL + "/choose_lines_middle_bg.png"}/>
        <div className={s.middleLInesName}>
          {this.props.data.name}
        </div>
        <div style={{'marginTop':'56px'}}>
          <div className={s.middleCell}>
            <div className={s.middleCellLeft}>
              <div className={s.middleLeftTable}>
                <img className={s.middleCellImage} src={STATIC_IMG_URL + "/choose_middle_bg.png"}/>
                
              </div>
            </div>
            <div className={s.middleCellRight}>
              {startView}
            </div>
          </div>
          <div style={{'fontSize':'0'}}>
            <img className={s.middleCellLine} src={STATIC_IMG_URL + "/choose_lines_line.png"}/>
          </div>
          <div className={s.middleCell}>
            <div className={s.middleCellLeft}>
              <div className={s.middleLeftTable}>
                <img className={s.middleCellImage} src={STATIC_IMG_URL + "/choose_middle_bg.png"}/>
                
              </div>
            </div>
            <div className={s.middleCellRight}>
              <div className={s.middleCellRightText}>
                {this.props.data.expendTime}
              </div>
            </div>
          </div>
          <div style={{'fontSize':'0'}}>
            <img className={s.middleCellLine} src={STATIC_IMG_URL + "/choose_lines_line.png"}/>
          </div>
          <div className={s.middleCell}>
            <div className={s.middleCellLeft}>
              <div className={s.middleLeftTable}>
                <img className={s.middleCellImage} src={STATIC_IMG_URL + "/choose_middle_bg.png"}/>
                
              </div>
            </div>
            <div className={s.middleCellRight}>
              <div className={s.middleCellRightText}>
                {this.props.data.selPercent}%
              </div>
            </div>
          </div>
          <div style={{'fontSize':'0'}}>
            <img className={s.middleCellLine} src={STATIC_IMG_URL + "/choose_lines_line.png"}/>
          </div>
          <div className={s.middleCell}>
            <div className={s.middleCellLeft}>
              <div className={s.middleLeftTable}>
                <img className={s.middleCellImage} src={STATIC_IMG_URL + "/choose_middle_bg.png"}/>
                
              </div>
            </div>
            <div className={s.middleCellRight}>
              <div className={s.middleCellRightText}>
                {this.props.data.cost}/
              </div>
            </div>
          </div>
          <div style={{'fontSize':'0'}}>
            <img className={s.middleCellLine} src={STATIC_IMG_URL + "/choose_lines_line.png"}/>
          </div>
        </div>
        <div className={s.middleBtnAllView}>
          <div className={s.middleBtn} onClick={() => {window.location.href ='http://localhost:63342/mainView/views/demo.html?_ijt=gr1fet0n5f8kb0ddbr5i0hll22'+'&id='+this.props.data.id}}>
            <img className={s.middleBtnBg} src={STATIC_IMG_URL + "/choose_middle_btn_bg.png"}/>
            
          </div>
          <div className={s.middleBtn} onClick={() => {window.location.href =this.props.data.buyTicketUrl}}>
            <img className={s.middleBtnBg} src={STATIC_IMG_URL + "/choose_middle_btn_bg.png"}/>
            
          </div>
        </div>
      </div>
    )
  }
}

class Cell extends Component{
  render(){
    return(
      <div onClick={() =>{this.props.goToDetails(this.props.data.id)}}>
        <img className={s.cellBg} src={STATIC_IMG_URL+"/choose_cell_bg.png"}/>
        <div className={s.cell}>
          <img className={s.cellPointImage} src={this.props.data.picture}/>
          <div className={s.cellLeftText}>
            {this.props.data.name}
          </div>
          <div>
            
          </div>
          <img className={s.cellRightArrow} src={STATIC_IMG_URL+"/choose_cell_arrow.png"}/>
        </div>
      </div>
    )
  }
}
const ConnectedComp = connect(
  store => ({
    RecommendLine: store.huashan.RecommendLine,
  }),
  dispatch =>({
    huashanAction: bindActionCreators(_huashanAction, dispatch),
    navigate: bindActionCreators(navigate, dispatch),
  })
)(ChooseLines);
export default ConnectedComp;



