import {} from '../constants';
import {dispatchAction, showDialog as _showDialog} from './actions'
import {ActionType, StateActionType} from '../utils/actions'
import {callApi} from '../core/api'

export const ModuleName = 'Trades';
export const GetTypeList = new StateActionType('getTypesList', ModuleName);
export const SelectedTab = new ActionType('SelectedTab', ModuleName);


export function getTypeList() {
  return (dispatch) => {
    dispatch(GetTypeList.newStartAction());

    return callApi('/tally/tallyTypeList', {synchroTime:null})
      .then(({data})=> {
        dispatch(GetTypeList.newSucceedAction({types:data.typeList}));
      }).catch(({message})=> {
        dispatch(GetTypeList.newErrorAction({error:message}));
    });
  }
}



