import {APP_PATH} from '../config'

// Pseudo action. All is handled through history module
export function replace(descriptor) {
  return (dispatch, _, { history }) => history.replace(APP_PATH+descriptor);
}

export function navigate(descriptor) {
  return (dispatch, _, { history }) => {
    history.push(APP_PATH+descriptor)
  };
}

export function go(n = -1) {
  return (dispatch, _, { history }) => {
    history.go(APP_PATH+n)
  };
}



