import React, {Component, PropTypes} from 'react';
import emptyFunction from 'fbjs/lib/emptyFunction';
import s from './App.css';
import {Provider, connect} from 'react-redux';
import {bindActionCreators} from 'redux'
import * as _appActions from '../../actions/appActions'
import Toast from '../../utils/toast'

class App extends Component {

  static propTypes = {
    context: PropTypes.shape({
      store: PropTypes.object.isRequired,
      setTitle: PropTypes.func,
    }).isRequired,
    children: PropTypes.element.isRequired,
    error: PropTypes.object,
  };

  static childContextTypes = {
    setTitle: PropTypes.func.isRequired,
  };

  getChildContext() {
    const context = this.props.context;
    return {
      setTitle: context.setTitle || emptyFunction,
    };
  }

  componentWillUnmount() {
  }

  render() {
    if (this.props.error) {
      return this.props.children;
    }
    const store = this.props.context.store;
    return (
      <Provider store={store}>
        <ConnectedAppContent >{this.props.children}</ConnectedAppContent>
      </Provider>
    );
  }
}

class AppContent extends Component{

  componentWillMount() {
    let initState = this.props.initState;
    if( initState == 'idle' || initState == 'error'){
      this.props.appActions.initApp();
    }
  }

  render(){
    let {initState, initError} = this.props;
    let content = null;
    if(initState=='done'){
      content = (
        <div>
          {this.props.children}
          <Toast />
        </div>
      );
    } else if (initState == 'error'){
      content = <div>{initError||''}</div>
    } else {
      //
      content = <div></div>
    }
    return content;
  }
}

const ConnectedAppContent = connect(
  store => ({
    initState: store.app.initState,
    initError:store.app.initError,
  }),
  dispatch =>({
    appActions: bindActionCreators(_appActions, dispatch),
  })
)(AppContent);

export default App;



