import React, {Component, PropTypes} from 'react';
import s from './ListItem.less';
import {IOType} from '../../constants'


export class ListLabelValueItem extends Component {

  static propTypes = {
    label: PropTypes.string, //
    value: PropTypes.string, //
    showDivider:PropTypes.bool
  };

  static defaultProps = {
    label: '',
    value: '',
    showDivider:true
  };

  render() {
    return (
      <div className={s.LabelValueItem}>
        <div className={s.content}>
          <div className={s.label}>{this.props.label}</div>
          <div className={s.value}>{this.props.value}</div>
        </div>
        {this.props.showDivider ? <hr className={s.divider} />:null}
      </div>);
  }
}

