import React, {Component, PropTypes} from "react";
import {connect} from "react-redux";
import {Flex} from 'antd-mobile'
import s from "./HomeTab.css";
import * as _mainActions from "../../actions/mainActions";
import {navigate} from "../../actions/routeActions";
import {bindActionCreators} from "redux";
import {STATIC_IMG_URL} from '../../config'

import {addDays} from '../../utils/timeUtils'

//echars
var echarts = require('echarts/lib/echarts');
// 
require('echarts/lib/chart/line');
require('echarts/lib/component/markPoint');

class HomeTab extends Component {

  constructor() {
    super();
    this.state = {
      weekChartSelectedIndex: -1,
      weekChartSelectedIn: null,
      weekChartSelectedOut: null,
    }
  }

  componentDidMount() {
    const weekChart = echarts.init(document.getElementById('weekChart'));

    let mockDate = [
      {date: '10.10', in: 10, out: 20},
      {date: '10.09', in: 0, out: 65},
      {date: '10.08', in: 0, out: 12},
      {date: '10.07', in: 50, out: 42},
      {date: '10.06', in: 2, out: 24},
      {date: '10.05', in: 28, out: 15},
      {date: '10.04', in: 5, out: 48}];
    let xValues = [];
    let yInValues = [];
    let yOutValues = [];

    for (let i = 0; i < mockDate.length; i++) {
      let item = mockDate[i];
      xValues[i] = item.date;
      yInValues[i] = {value:item.in};
      yOutValues[i] = {value:item.out};
    }

    // 
    let option = {
      title: {
        show:false,
      },
      legend: {
        show:false,
      },
      tooltip:{
        show:false,
      },
      grid:{
        top: 20,
        right: 30,
        bottom: 30,
        left: 30,
      },
      xAxis: {
        data: xValues.reverse(),
        boundaryGap:false,
        axisLine:{
          show:true,
          lineStyle:{
            color:'#d6e1e5'
          }
        },
        axisLabel:{
          textStyle:{
            color:'#999',
            fontSize:20,
          },
          formatter: function (value, index) {
            if(index == 0){
              return '';
            }
            return value;
          }
        },
        splitLine:{
          show:true,
          lineStyle:{
            color:'#d6e1e5'
          }
        },

      },
      yAxis: {
        type: 'value',
        boundaryGap:[0,0],
        splitNumber:4,
        minInterval:1,
        axisLine:{
          show:true,
          lineStyle:{
            color:'#d6e1e5'
          }
        },
        axisLabel:{
          inside:true,
          textStyle:{
            color:'#999',
            fontSize:20,
          }
        },
        splitLine:{
          show:true,
          lineStyle:{
            color:'#d6e1e5'
          }
        },
      },
      series: [
        {
        name: '',
        type: 'line',
        smooth: true,
        smoothMonotone: 'x',
        data: yInValues,
        lineStyle: {
          normal: {
            width: 4,
            color: '#46c4ff',
          }
        },
        areaStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: '#afe5ff'
            }, {
              offset: 1,
              color: 'rgba(255, 255, 255, 0)'
            }]),
          }
        },
        symbol: 'image://http://localhost:3000/img/home_chart_sym_in_nor.png',
        symbolSize: 10,
      },
        {
        name: '',
        type: 'line',
        smooth: true,
        smoothMonotone: 'x',
        data: yOutValues,
        lineStyle: {
          normal: {
            width: 4,
            color: '#71bc79',
          }
        },
        areaStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: '#cdfad2'
            }, {
              offset: 1,
              color: 'rgba(255, 255, 255, 0)'
            }]),
          }
        },
        symbol: 'image://http://localhost:3000/img/home_chart_sym_out_nor.png',
        symbolSize: 10,
      }]
    };

    // 
    weekChart.setOption(option);
    weekChart.on('click',  (params) => {
      console.dir(params);
      if (params.componentType != 'series') {
        return;
      }

      const normalInState = {
        symbol: 'image://http://localhost:3000/img/home_chart_sym_in_nor.png',
        symbolSize: 10
      };
      const selectedInState = {
        symbol: 'image://http://localhost:3000/img/home_chart_sym_in_sel.png',
        symbolSize: 16
      };
      const normalOutState = {
        symbol: 'image://http://localhost:3000/img/home_chart_sym_out_nor.png',
        symbolSize: 10
      };
      const selectedOutState = {
        symbol: 'image://http://localhost:3000/img/home_chart_sym_out_sel.png',
        symbolSize: 16
      };

      if (params.dataIndex != this.state.weekChartSelectedIndex) {
        if(this.state.weekChartSelectedIndex>=0){
          //
          let index = this.state.weekChartSelectedIndex;
          yInValues[index] = Object.assign(yInValues[index],normalInState);
          yOutValues[index] = Object.assign(yOutValues[index],normalOutState);
        }
        //
        let index = params.dataIndex;
        yInValues[index] = Object.assign(yInValues[index],selectedInState);
        yOutValues[index] = Object.assign(yOutValues[index],selectedOutState);
        this.setState({
          weekChartSelectedIndex:index,
          weekChartSelectedIn:yInValues[index],
          weekChartSelectedOut:yOutValues[index],
        });
      } else {
        //
        let index = params.dataIndex;
        yInValues[index] = Object.assign(yInValues[index],normalInState);
        yOutValues[index] = Object.assign(yOutValues[index],normalOutState);
        this.setState({
          weekChartSelectedIndex:-1,
          weekChartSelectedIn:null,
          weekChartSelectedOut:null,
        });
      }

      option.series[0].animation = false;
      option.series[1].animation = false;
      weekChart.setOption(option);
    });
  }

  render() {
    return (
      <div className={s.root}>
        <div className={s.month} style={{backgroundImage: 'url("' + STATIC_IMG_URL + '/index_bg_header.png")'}}>
          <div className={s.monthIO}>
            <div className={s.monthIOItem}>
              <div className={s.monthIOLabel}></div>
              <div className={s.monthIOAmount}>123456.12</div>
            </div>
            <div className={s.monthIOItem}>
              <div className={s.monthIOLabel}></div>
              <div className={s.monthIOAmount}>123456.12</div>
            </div>
          </div>
          <div className={s.budget}>
            <img className={s.budgetBg} src={STATIC_IMG_URL + '/index_balance_bg.png'}/>
            <div className={s.budgetLabel}></div>
            <div className={s.budgetAmount}>7900.00</div>
            <div className={s.budgetTotal}>10000.00</div>
          </div>
        </div>

        <div className={s.weak}>
          {this.state.weekChartSelectedIndex >=0 ?
            (<div className={s.weekSel}>
              <i className={s.weekSelInDot}/> <div className={s.weekSelIn}>:{this.state.weekChartSelectedIn.value.toFixed(2)}</div>
              <i className={s.weekSelOutDot}/> <div className={s.weekSelOut}>:{this.state.weekChartSelectedOut.value.toFixed(2)}</div>
            </div>):(
              <div className={s.weekChartTitle}>
                
              </div>
          )}
          <div className={s.weekChart} id="weekChart">
          </div>
          <hr className={s.weakDivider}/>
        </div>

        <div className={s.list}>
          <div className={s.lt}>
            <img className={s.ltIc} src={STATIC_IMG_URL+"/index_ic_today.png"}/>
            <div className={s.ltLeft}>
              <div className={s.ltLeft1}></div>
              <div className={s.ltLeft2}></div>
            </div>
            <div className={s.ltRight}>
              <div className={s.ltRight1}>0.00</div>
              <div className={s.ltRight2}>0.00</div>
            </div>
            <img className={s.ltArrow} src={STATIC_IMG_URL+"/common_ic_arrow_right.png"}/>
          </div>
          <hr className={s.ltDivider}/>

          <div className={s.lt}>
            <img className={s.ltIc} src={STATIC_IMG_URL+"/index_ic_financing.png"}/>
            <div className={s.ltLeft}>
              <div className={s.ltLeft1}></div>
              <div className={s.ltLeft2}></div>
            </div>
            <div className={s.ltRight}>
              <div className={s.ltRight1}>+0.00</div>
            </div>
            <img className={s.ltArrow} src={STATIC_IMG_URL+"/common_ic_arrow_right.png"}/>
          </div>
          <hr className={s.ltDivider}/>

          <div className={s.lt}>
            <img className={s.ltIc} src={STATIC_IMG_URL+"/index_ic_tools.png"}/>
            <div className={s.ltLeft}>
              <div className={s.ltLeft1}></div>
              <div className={s.ltLeft2}>/</div>
            </div>
            <img className={s.ltArrow} src={STATIC_IMG_URL+"/common_ic_arrow_right.png"}/>
          </div>
          <hr className={s.ltDivider}/>
        </div>
      </div>
    )
  }
}

export default HomeTab;


