import {Toast} from 'antd-mobile'

const loadingProgress = {
  success (content="!", duration, onClose) {
    Toast.hide();
    Toast.success(content, duration, onClose);
  },
  fail (content='!', duration, onClose) {
    Toast.hide();
    Toast.fail(content, duration, onClose);
  },
  info (content='', duration, onClose) {
    Toast.hide();
    Toast.info(content, duration, onClose);
  },
  loading (content='...', duration=90000, onClose) {
    Toast.hide();
    Toast.loading(content, duration, onClose);
  },
  offline (content='', duration, onClose) {
    Toast.hide();
    Toast.offline(content, duration, onClose);
  },
  hide(){
    Toast.hide();
  }

};


export default loadingProgress;


