import {ActionHandlerManager, StateActionHandler, ActionHandler} from '../utils/actions'

class ShowDialogHandler extends ActionHandler {

  constructor(actionType){
    super(actionType);
  }

  handle(state, action) {
    let name = action.dialog.name;
    if(!name){
      console.log('dialog action has not name property');
      return state;
    }

    const newState = Object.assign({}, state);
    newState[name] = Object.assign({}, newState[name], action.dialog);
    return newState;
  }
}

export {
  ShowDialogHandler
}



