export function addDays(date,days){
  let nd = new Date(date);
  nd = nd.valueOf();
  nd = nd + days * 24 * 60 * 60 * 1000;
  nd = new Date(nd);
  return nd;
}

export function isSameDay(d1:Date, d2:Date) {
  if(!d1 || !d2){
    return false;
  }

  return d1.getYear() == d2.getYear()
    && d1.getMonth() == d2.getMonth()
    && d1.getDate() == d2.getDate();
}

/**
 * yyyy-MM-dd HH:mm:ss.[SSS]
 * @constructor
 */
export function dateFromStr(strDate:String):Date {
  if(!strDate || strDate.trim().length == 0){
    return null;
  }

  return new Date(strDate.replace(/-/g,"/"));
}

export function formatToyyyyMMdd(date, yearUnit='', monthUnit='', dayUnit='') {
  if(!date){
    return null;
  }
  if(typeof date === 'string'){
    date = dateFromStr(date);
  }
  return date.getFullYear()+yearUnit+(date.getMonth()+1)+monthUnit+date.getDate()+dayUnit;
}


