import React, {Component, PropTypes} from 'react';
import s from './Amount.css';
import {IOType} from '../../constants'


class Amount extends Component {

  static propTypes = {
    ioType: PropTypes.number, //
    amount: PropTypes.number, //
    unit: PropTypes.string, //
    color: PropTypes.bool //
  };

  static defaultProps = {
    ioType:null,
    unit:null,
    color:true
  };

  render() {
    let color = null;
    if(this.props.color){
      if(this.props.ioType == IOType.INCOME){
        color = s.income;
      } else if(this.props.ioType == IOType.OUTLAY) {
        color = s.outlay;
      } else {
      }
    }
    return (<span className={color}>{this.props.amount.toFixed(2)}{this.props.unit}</span>);
  }
}


export default Amount;



