export class ActionType {
  constructor(actionName, moduleName) {
    this.moduleName = moduleName ? moduleName : "";
    this.actionName = this.moduleName + '.' + actionName;
  }

  newAction(payload) {
    const action = {
      type: this.actionName,
    };

    if (payload) {
      if(typeof payload != 'object'){
        console.error('action payload should a object. but is a '+ typeof payload);
      }
      Object.assign(action, payload)
    }
    return action;
  }
}

export class StateActionType extends ActionType {

  constructor(actionName, moduleName) {
    super(actionName, moduleName);
    this.StateStart = this.moduleName + '.' + actionName + '.STATE_START';
    this.StateSucceed = this.moduleName + '.' + actionName + '.STATE_SUCCEED';
    this.StateError = this.moduleName + '.' + actionName + '.STATE_ERROR';
    // this.StateCanceled = actionName + '.STATE_CANCELED';
  }

  newStartAction(payload) {
    const action = super.newAction(payload);
    action.type = this.StateStart;
    return action;
  }

  newSucceedAction(payload) {
    const action = super.newAction(payload);
    action.type = this.StateSucceed;
    return action;
  }

  newErrorAction(payload) {
    const action = super.newAction(payload);
    action.type = this.StateError;
    return action;
  }
}

export class ActionHandler {

  constructor(actionType, handleFuncs = undefined) {
    this.actionType = actionType;
    if (handleFuncs && handleFuncs.handle) {
      this.handle = handleFuncs.handle;
    }
  }

  isHandlerOfAction(actionName) {
    return this.actionType.actionName == actionName;
  };

  handle(state, action) {
    console.log("warm: dispatch method not override");
  }

}

export class StateActionHandler extends ActionHandler {

  constructor(actionType, handleFuncs = undefined) {
    super(actionType, handleFuncs);

    if (handleFuncs) {
      Object.assign(this, handleFuncs);
    }
  }

  isHandlerOfAction(actionName) {
    return actionName.startsWith(this.actionType.actionName + '.');
  }

  onStart = (state, action) => {
    return state;
  };

  onSucceed = (state, action) => {
    return state;
  };

  onError = (state, action) => {
    return state;
  };

  // onCancel(state, action){};

  onFinished = (state, action) => {
    return state;
  };

  handle(state, action) {

    let newState = state;

    if (action.type.endsWith('.STATE_START')) {
      newState = this.onStart(newState, action);

    } else if (action.type.endsWith('.STATE_SUCCEED')) {
      newState = this.onSucceed(newState, action);
      newState = this.onFinished(newState, action);

    } else if (action.type.endsWith('.STATE_ERROR')) {
      newState = this.onError(newState, action);
      newState = this.onFinished(newState, action);
    }

    return newState;
  };
}

/**
 * handlers,Action
 */
export class ActionHandlerManager {
  constructor() {
    this.handlers = [];
  }

  addHandler(handler) {
    if (!this.handlers.includes(handler)) {
      this.handlers.push(handler);
    }
  }

  removeHandler(handler) {
    let index = this.handlers.indexOf(handler);
    if (index >= 0) {
      this.handlers.splice(index, 1);
    }
  }


  dispatch(state, action) {

    for (let handler of this.handlers) {
      if (handler && handler.isHandlerOfAction(action.type)) {
        return handler.handle(state, action);
      }
    }
    return state;
  }
}



