import React, {Component, PropTypes} from "react";
import ReactDOM from "react-dom";
import s from "./NumberInput.less";
// import * as _typeAction from "../../actions/typesActions";

class NumberInput extends Component {

  static defaultProps = {
    value: 123456.78,    //
    placeHolder: '',
  };

  constructor() {
    super();

    this.state = {
      beforeValue: null, //
      value: null,
      cursor: true,
      cursorPos: 0,
      fontSize: null,
    }
  }

  componentDidMount() {
    // //div.currentStyle['font-size']; IE
    // //window.getComputedStyle(div).fontSize  Chrome
    let fontSize = window.getComputedStyle(ReactDOM.findDOMNode(this.refs.root)).fontSize;
    this.setState({
      value: this.props.value,
      cursorPos: this.getStrValue(this.props.value).length - 3,
      fontSize,
    });

  }

  getStrValue = (value)=> {
    if (value === undefined || value === null) {
      return '';
    } else {
      return value.toString();
    }
  };

  addNumber = (position, value)=> {
    let strValue = this.getStrValue(this.state.value);
    if (strValue == '') {
      //
      if (value == '.') {
        value = '0.'
      }
      strValue = strValue + value;
    } else if (strValue = '0') {
      if (value == '0') {
      } else if (value == '.') {
        strValue = strValue + '.';
      } else {
        strValue = value;
      }
    } else {
      if (value == '.') {
        if (strValue.indexOf('.') >= 0) {
          //
        } else {
          strValue = strValue.substring(0, position) + value + strValue.substring(position);
        }
      } else {
        strValue = strValue.substring(0, position) + value + strValue.substring(position);
      }
    }

    this.setState({value: Number(strValue), cursorPos: position + 1});
    this.onChange(Number(strValue));
  };

  deleteNumber = (position) => {
    position = position - 1;
    let strValue = this.getStrValue(this.state.value);
    let deleteValue = strValue[position];
    if (deleteValue == '.') {
      position = position - 1;
      deleteValue = strValue[position];
    }
    strValue = strValue.substring(0, position) + strValue.substring(position + 1);

    this.setState({value: Number(strValue), cursorPos: position});
    this.onChange(Number(strValue));
  };

  clear = ()=> {
    this.setState({value: Number(0), cursorPos: 1, beforeValue: 0});
    this.onChange(0);
  };

  onEnter = (value)=> {
    if (this.props.onEnter) {
      this.props.onEnter(value);
    }
  };

  onChange = (value) => {
    if (this.props.onChange) {
      this.props.onChange(value);
    }
  };

  startPlus = ()=> {
    this.setState({beforeValue: this.state.value});
  };
  calcPlus = ()=> {
    let value = this.state.beforeValue + this.state.value;
    this.setState({value: value, beforeValue: value, cursorPos: this.getStrValue(value).length});
  };


  senKey = (keyCode) => {
    let {cursorPos} = this.state;
    if (49 <= keyCode && keyCode <= 57) {
      this.addNumber(cursorPos, (keyCode - 49) + '');
    } else if (97 <= keyCode && keyCode <= 105) {
      this.addNumber(cursorPos, (keyCode - 97) + '');
    } else if (110 == keyCode) {
      //
      this.addNumber(cursorPos, '.');
    } else if (8 == keyCode) {
      //BackSpace
      this.deleteNumber(cursorPos);
    } else if (12 == keyCode) {
      //Clear
      this.clear();
    } else if (108 == keyCode) {
      //Enter
      this.onEnter(this.state.value);
    } else if (107 == keyCode) {
      // +
      this.startPlus();
    } else if (187 == keyCode) {
      // =
      this.calcPlus();
    }
  };


  render() {
    let content = [];
    let {value, cursorPos} = this.state;
    let {placeHolder} = this.props;

    let cursor = this.state.cursor ?
      <span key='cursor' ref="cursor" className={s.cursor} style={{height: this.state.fontSize}}/> : null;

    if (value === undefined || value === null) {
      content.push(<div key='placeHolder' className={s.placeHolder}>{placeHolder || ''}<span ref="cursor"
                                                                                             className={s.cursor}
                                                                                             style={{height: this.state.fontSize}}/>
      </div>);
    } else {
      let strValue = value.toString();
      for (let i = 0; i < strValue.length; i++) {
        content.push(
          <div className={s.item} key={i}>
            {cursorPos == i ?
              <span className={s.cursor} style={{height: this.state.fontSize}}/> : null}
            {strValue[i]}
          </div>)
      }
      if (cursorPos >= strValue.length) {
        content.push(
          <div className={s.cursorItem} key='cursorItem' style={{height: this.state.fontSize}}>
            <span className={s.cursor}/> {''}
          </div>);
      }
    }

    return <div ref="root"
                className={s.NumberInput + (this.props.className ? ' ' + this.props.className : '')}>{content}</div>
  }
}


export default NumberInput;


