import React, {Component, PropTypes} from 'react';
import s from './RelativeModule.less';
import {IOType} from '../../constants'
import {TypeDefaultImage} from '../../constants'


export class RelativeModule extends Component {

  static propTypes = {
    icon: PropTypes.string,
    title: PropTypes.string, //
    button: PropTypes.string, //
    showDivider:PropTypes.bool
  };

  static defaultProps = {
    icon: TypeDefaultImage,
    title: '',
    button: '',
    showDivider:true
  };

  render() {
    return (
      <div className={s.root + (this.props.className ? ' '+ this.props.className : '')}>
        {this.props.showDivider ? <hr className={s.divider} />:null}
        <div className={s.content}>
          <img className={s.icon} src={this.props.icon}/>
          <div className={s.title}>{this.props.title}</div>
          <div className={s.button}>{this.props.button}</div>
        </div>
      </div>);
  }
}

export default RelativeModule;




