import {} from '../constants';
import {dispatchAction, showDialog as _showDialog} from './actions'
import {ActionType, StateActionType} from '../utils/actions'
import loadingProgress from '../utils/loadingProgress'
import {callApi} from '../core/api'
import {navigate} from './routeActions'

export const ModuleName = 'Huashan';
export const GetPointDetailed =new StateActionType('GetPointDetailed', ModuleName);
export const GetAllLines =new StateActionType('GetAllLines', ModuleName);


/**
 * 
 */
export function getPointDetailed(id) {
  return (dispatch) => {
    dispatch(GetPointDetailed.newStartAction());
    let params= {id:id};
    loadingProgress.loading('');
    return callApi('/pointInfo/getPointDetailed',params)
      .then(({data})=> {
        const p = Object.assign({pointDetialed:data.data});
        dispatch(GetPointDetailed.newSucceedAction(p));
        loadingProgress.hide();
      }).catch(({message})=>{
        dispatch(GetPointDetailed.newErrorAction({error:message}));
        loadingProgress.info('');
      });
  }
}

/**
 * 
 */
export function getAllLines() {
  return (dispatch) => {
    dispatch(GetAllLines.newStartAction());
    loadingProgress.loading('');
    return callApi('/recommendLine/getAll')
      .then(({data})=> {
        const p = Object.assign({RecommendLine:data.data});
        dispatch(GetAllLines.newSucceedAction(p));
        loadingProgress.info('',1000);
        loadingProgress.hide();
      }).catch(({message})=>{
        dispatch(GetAllLines.newErrorAction({error:message}));
        loadingProgress.info('',1000);
      });
  }
}



