import React, {Component, PropTypes} from "react";
import {connect} from "react-redux";
import * as _huashanAction from "../../actions/huashanAction";
import {navigate} from "../../actions/routeActions";
import {bindActionCreators} from "redux";
import s from "./ScenicDetail.css"
import {STATIC_IMG_URL} from '../../config'
import { Modal,WhiteSpace, WingBlank, Flex, Carousel } from 'antd-mobile';

class ScenicDetail extends Component{
  componentWillMount() {
    this.props.huashanAction.getPointDetailed(this.props.id);
  }
  render(){
    this.windowHeight=window.innerHeight;
    var middleStyle={
      "height":this.windowHeight+"px",
    }
    const settings = {
      dots: true,
      autoplay:true,
      infinite: true,
      selectedIndex: 2,
    };
    var table=[];
    var picturlView;
    if(this.props.pointDetialed != undefined){
      if(this.props.pointDetialed.pointPiture != undefined){
        table=this.props.pointDetialed.pointPiture.map(function (data,index) {
          return(
            <Flex
              key = {index}
              justify="center"
              className={s.slideImage}
            >
              <img className={s.slideImage} src={data.picture}/>
            </Flex>
          )
        }.bind(this))
        picturlView=
          <Carousel {...settings}>
          {table}
        </Carousel>
      }
    }
    return(
      <div>
        <img className={s.pointMiddle} style={middleStyle} src={STATIC_IMG_URL + "/huashan_pain_middle.png"}/>
        <div className={s.slideAll}>
          {picturlView}
        </div>
        <div className={s.sceneDes}>
          {this.props.pointDetialed?this.props.pointDetialed.pointTxt?this.props.pointDetialed.pointTxt.document:'':''}
        </div>
        <img className={s.pointTop} src={STATIC_IMG_URL + "/huashan_paint_top.png"}/>
        <img className={s.pointBottom} src={STATIC_IMG_URL + "/huashan_pain_bottom.png"}/>
      </div>
    )
  }
}
const ConnectedComp = connect(
  store => ({
    pointDetialed: store.huashan.pointDetialed,
  }),
  dispatch =>({
    huashanAction: bindActionCreators(_huashanAction, dispatch),
    navigate: bindActionCreators(navigate, dispatch),
  })
)(ScenicDetail);
export default ConnectedComp;



