import {} from '../constants';
import {dispatchAction, showDialog as _showDialog} from './actions'
import {ActionType, StateActionType} from '../utils/actions'
import loadingProgress from '../utils/loadingProgress'
import {callApi} from '../core/api'
import {navigate} from './routeActions'

export const ModuleName = 'Trades';
export const GetTradeList = new StateActionType('GetTradeList', ModuleName);
export const GetTradeDetail = new StateActionType('GetTradeDetail', ModuleName);
export const SelectedTab = new ActionType('SelectedTab', ModuleName);
export const EditingTrade = new ActionType('EditingTrade', ModuleName);


export function getTradeList(tallyYear, tallyMonth, pageIndex, pageSize) {
  return (dispatch) => {
    let reqParams = {tallyYear, tallyMonth, pageIndex, pageSize};

    let actionParam = {year: tallyYear, month: tallyMonth, pageIndex, pageSize};

    dispatch(GetTradeList.newStartAction(actionParam));

    callApi('/tally/bookkeeping/tallyListPage?' + pageIndex, reqParams)
      .then(({data})=> {
        const d = Object.assign(data, actionParam);
        dispatch(GetTradeList.newSucceedAction(d))
      }).catch(({message})=> {
      dispatch(GetTradeList.newErrorAction({...actionParam, error: message}))
    });
  }
}


export function getTradeDetail(tradeId) {
  return dispatch => {
    loadingProgress.loading();
    dispatch(GetTradeDetail.newStartAction({tradeId}));

    return callApi('/tally/bookkeeping/tallySelect', {tallyId: tradeId})
      .then(({data})=> {
        dispatch(GetTradeDetail.newSucceedAction({trade: data}));
        loadingProgress.hide();
        return {trade:data}
      })
      .catch(({message}) => {
        dispatch(GetTradeDetail.newErrorAction({error: message}))
        loadingProgress.fail(message);
        return {trade:null}
      });
  }
}

export function editTrade(trade, naviToEditor=true) {

  return dispatch => {
    dispatch(EditingTrade.newAction({trade}));
    if(naviToEditor){
      if(trade.tallyId > 0){
        dispatch(navigate(`/trades/${trade.tallyId}/edit`))
      } else {
        dispatch(navigate(`/trades/add`))
      }
    }
  }

}


