import React from 'react';
import EmailLogin from './EmailLogin';
import EmailBill from './EmailBill';
import EmailCreditCard from './EmailCreditCardDetials';

export default {

  path: '/emailImport',
  children:[
    {
      path:'/loginIn',
      action() {
        return <EmailLogin />;
      }
    },
    {
      path:'/creditCard',
      action() {
        return <EmailCreditCard />;
      }
    },
    {
      path:'/emailBill',
      action() {
        return <EmailBill />;
      }
    }
  ],
  action({next}){
    return next();
  }
};



