import React from 'react';
import TradeDetail from './TradeDetail';
import TradeEdit from './TradeEdit';

export default {
  path: '/trades',
  children:[
    {
      path:'/add',
      action({context, params, query}) {
        return <TradeEdit tradeId={0}/>;
      }
    },
    {
      path:'/:tradeId',
      action({context, params, query}) {
        return <TradeDetail tradeId={Number(params.tradeId)}/>;
      }
    },
    {
      path:'/:tradeId/edit',
      action({context, params, query}) {
        return <TradeEdit tradeId={Number(params.tradeId)}/>;
      }
    }
  ],
  action({next}){
    return next();
  }
};



