import React, {Component} from "react";
import {Modal as _Modal} from "antd-mobile";
import s from './Modal.css'

export class Modal extends Component {

  static defaultProps = {
    transparent: true,
    maskClosable: true,
    closable: false,
  };

  static alert(title, message, actions) {
    _Modal.alert(title, message, actions);
  }

  static prompt(title, message, callbackOrActions, type) {
    _Modal.prompt(title, message, callbackOrActions, type);
  }

  render() {
    return (
      <_Modal {...this.props}>
        {this.props.children}
      </_Modal>
    )
  }
}



