import React, {Component, PropTypes} from "react";
import {connect} from "react-redux";
import * as _emailAction from "../../actions/emailAction";
import {navigate} from "../../actions/routeActions";
import {bindActionCreators} from "redux";
import s from "./EmailBill.css"
import flex from "./EmailAllStyle.css"
import {STATIC_IMG_URL} from '../../config'
import SwipeOut from '../../components/SwipeOut'

class EmailBill extends Component{

  componentDidMount() {
    this.props.emailAction.getEmailList();
  }
  updateEmail(emailAddress){
    this.props.emailAction.upDateEmail(emailAddress)
  }
  deleteTable(email,emailId) {
    this.props.emailAction.deleteEmailList(email,emailId);
  }
  render(){
    var table=[];
    if(this.props.emailList != undefined){
      table=this.props.emailList.map(function (data,index) {
        return(
          <EmailBillTable
            key={index}
            billData={data}
            emailId={data.emailId}
            delete={this.deleteTable.bind(this)}
            updata={this.updateEmail.bind(this)}
          />
        )
      }.bind(this))
    }
    return(
      <div>
        {table}
      </div>
    )
  }
}

class EmailBillTable extends Component{
  updateClick(){
    this.props.updata(this.props.billData.email);
  }
  render(){
    var cell=[];
    cell=this.props.billData.creditCardBillInfoList.map(function (asset,index) {
      return(
        <EmailBillCell
          key={index}
          cellData={asset}
        />
      )
    }.bind(this))
    let updateview;
    if(this.props.billData.isUpdate) {
      updateview =
        <div className={s.updateAll}>
          <img className={s.updateimg} src={STATIC_IMG_URL + '/email_updating_bg.png'}/>
          <div className={s.updateSqure}/>
        </div>
    }else {
      updateview =
      <div className={s.updateBtn + ' ' + flex.flexContainer + ' ' + flex.vCenter + ' ' + flex.hCenter} onClick={this.updateClick.bind(this)}>
        
      </div>
    }
    return(
      <div>
        <div className={s.grayView}/>
        <SwipeOut
          right={[
            {
              text: "",
              onPress: () => {
                this.props.delete(this.props.billData.email,this.props.emailId);
              },
              style: {backgroundColor: 'red', color: 'white'}
            }]}>
          <div
            className={s.topTable + ' ' + flex.flexContainer + ' ' + flex.flexContainerWrap + ' ' + flex.hBoxOrient + ' ' + flex.hJustify + ' ' + flex.vCenter}>
            <div className={flex.flexContainer + ' ' + flex.vCenter + ' ' + flex.vBoxOrient + ' ' + flex.vStretch}>
              <div className={s.emailAddress}>
                {this.props.billData.email}
              </div>
              <div className={s.emailUpdateTime}>
                {this.props.billData.updateTimeMillis}
              </div>
            </div>
            {updateview}
          </div>
        </SwipeOut>
        {cell}
      </div>
    )
  }
}
class EmailBillCell extends Component{
  render(){
    return(
      <div>
        <div className={s.cell}>
          <img className={s.cellIcn} src={STATIC_IMG_URL+"/index_ic_today.png"}/>
          <div className={s.cellMiddle}>
            <div className={s.cellTopText}>
              <div className={s.cellTopText1}>
                {this.props.cellData.bankName}
              </div>
              <div className={s.cellTopText2}>
                {this.props.cellData.tailNumber}
              </div>
              <div className={s.cellTopText2}>
                {this.props.cellData.cardUserName}
              </div>
            </div>
            <div className={s.cellBottomText}>
              <div className={s.cellBottomText1}>{this.props.cellData.newBalance}</div>
            </div>
          </div>
          <img className={s.ltArrow} src={STATIC_IMG_URL+"/common_ic_arrow_right.png"}/>
        </div>
        <hr className={s.cellline}/>
      </div>
    )
  }
}

const ConnectedComp = connect(
  store => ({
    emailList: store.email.emailList,
  }),
  dispatch =>({
    emailAction: bindActionCreators(_emailAction, dispatch),
    navigate: bindActionCreators(navigate, dispatch),
  })
)(EmailBill);
export default ConnectedComp;



