import React, {Component} from 'react'
import Hammer from 'react-hammerjs'
import s from './SwipeOut.css'


class SwipeOut extends Component {
  constructor() {
    super();

    this.state = {
      openedLeft: false,
      openedRight: false
    };
  }

  static defaultProps = {
    disabled: false,
    left: [],
    right: [],
    onOpen: function onOpen() {
    },
    onClose: function onClose() {
    },
  };

  static propTypes = {
    disabled: React.PropTypes.bool,
    left: React.PropTypes.arrayOf(React.PropTypes.object),
    right: React.PropTypes.arrayOf(React.PropTypes.object),
    onOpen: React.PropTypes.func,
    onClose: React.PropTypes.func,
    children: React.PropTypes.any
  };

  componentDidMount() {
    let left = this.props.left;
    let right = this.props.right;

    let width = this.refs.content.offsetWidth;

    this.contentWidth = width;

    // this.btnsLeftWidth = left.length > 0 ? width / 5 * left.length : 0;
    // this.btnsRightWidth = right.length > 0 ? width / 5 * right.length : 0;

    if (left.length > 0) {
      this.btnsLeftWidth = width / 5 * left.length;
      this.refs.left.style.width = this.btnsLeftWidth + 'px';
    }
    if (right.length > 0) {
      this.btnsRightWidth = width / 5 * right.length;
      this.refs.right.style.width = this.btnsRightWidth + 'px';
    }


    this.btnsLeftWidth = left.length > 0 ? this.refs.left.offsetWidth : 0;
    this.btnsRightWidth = right.length > 0 ? this.refs.right.offsetWidth : 0;
  };

  onPanStart = (e) => {
    if (this.props.disabled) {
      return;
    }
    this.panStartX = e.deltaX;

  };

  onPan = (e) => {
    if (this.props.disabled) {
      return;
    }

    // get pan distance
    let posX = e.deltaX - this.panStartX;
    if (this.state.openedRight) {
      posX = posX - this.btnsRightWidth;
    } else if (this.state.openedLeft) {
      posX = posX + this.btnsLeftWidth;
    }

    if (posX < 0 && this.props.right) {
      this.setStyle(Math.min(posX, 0));
    } else if (posX > 0 && this.props.left) {
      this.setStyle(Math.max(posX, 0));
    }
  };

  onPanEnd = (e) => {
    if (this.props.disabled) {
      return;
    }

    let posX = e.deltaX - this.panStartX;
    let contentWidth = this.contentWidth;
    let btnsLeftWidth = this.btnsLeftWidth;
    let btnsRightWidth = this.btnsRightWidth;
    let openX = contentWidth * 0.33;
    let openLeft = posX > openX || posX > btnsLeftWidth / 2;
    let openRight = posX < -openX || posX < -btnsRightWidth / 2;

    if (this.state.openedRight) {
      openRight = posX - openX < -openX;
    }
    if (this.state.openedLeft) {
      openLeft = posX + openX > openX;
    }

    if (openRight && posX < 0) {
      this.open(-btnsRightWidth, false, true);
    } else if (openLeft && posX > 0) {
      this.open(btnsLeftWidth, true, false);
    } else {
      this.close();
    }
  };

  onTap = (e) => {
    if (this.state.openedLeft || this.state.openedRight) {
      e.preventDefault();
      this.close();
    } else {
      if(this.props.onClick){
        this.props.onClick(e);
      }
    }
  };

  onLongPress = (e) => {
    console.log('>>>>>>onLongPress');
    this.open(-this.btnsRightWidth, false, true);
  };

  open = (value, openedLeft, openedRight) => {
    if (!this.state.openedLeft && !this.state.openedRight) {
      if (this.props.onOpen) {
        this.props.onOpen();
      }
    }

    this.setState({
      openedLeft: openedLeft,
      openedRight: openedRight
    });
    this.setStyle(value);
  };


  close = () => {
    if (this.state.openedRight || this.state.openedRight) {
      if (this.props.onClose) {
        this.props.onClose();
      }
    }

    this.setState({
      openedLeft: false,
      openedRight: false
    });
    this.setStyle(0);
  };

  getContentEasing = (value, limit) => {
    // limit content style left when value > actions width
    if (value < 0 && value < limit) {
      return limit - Math.pow(limit - value, 0.7);
    } else if (value > 0 && value > limit) {
      return limit + Math.pow(value - limit, 0.7);
    }
    return value;
  };


  setStyle = (value) => {
    let left = this.props.left;
    let right = this.props.right;

    let limit = value > 0 ? this.btnsLeftWidth : -this.btnsRightWidth;
    let contentLeft = this.getContentEasing(value, limit);
    // this.refs.content.style.left = contentLeft + 'px';
    this.refs.content.style.transform = 'translateX(' + contentLeft + 'px)';

    //,actions
    // if (left.length) {
    //   let leftWidth = Math.max(Math.min(value, Math.abs(limit)), 0);
    //   this.refs.left.style.width = leftWidth + 'px';
    // }
    // if (right.length) {
    //   let rightWidth = Math.max(Math.min(-value, Math.abs(limit)), 0);
    //   this.refs.right.style.width = rightWidth + 'px';
    // }
  };

  onButtonClick = (btn) => {
    let close = true;
    if (btn.onPress) {
      close = !(btn.onPress() === true);
    }
    if (close) {
      this.close()
    }
  };

  render() {

    let leftActions = null, rightActions;
    let left = this.props.left;
    let right = this.props.right;

    if (right.length) {
      rightActions = (
        <div key="right" ref="right"
             className={s.actions + " " + s.actionsRight + " " + ( s.anim ) }>
          {right.map((item, index) => (
            <div key={item.text} className={s.actionButton} style={item.style}
                 onClick={this.onButtonClick.bind(this, item)}>
              <span className={s.actionButtonText}>{item.text}</span>
            </div>
          ))}
        </div>
      );
    }

    if (left.length) {
      leftActions = (
        <div key="left" ref="left"
             className={s.actions + " " + s.actionsLeft + " " + ( s.anim ) }>
          {left.map((item, index) => (
            <div key={item.text} className={s.actionButton}>
              <span className={s.actionButtonText}>{item.text}</span>
            </div>
          ))}
        </div>
      );
    }

    //
    let direction = 'DIRECTION_HORIZONTAL';
    if (left.length > 0 && right.length === 0 && !this.state.openedLeft) {
      direction = 'DIRECTION_RIGHT';
    }
    if (right.length > 0 && left.length === 0 && !this.state.openedRight) {
      direction = 'DIRECTION_LEFT';
    }

    return (
      <div className={s.swipeOut} style={this.props.style}>
        {leftActions}
        {rightActions}

        <Hammer
          direction={direction}
          onPanStart={this.onPanStart}
          onPan={this.onPan}
          onPanEnd={this.onPanEnd}
          onTap={this.onTap}
          onPress={this.onLongPress}
          options={{
            recognizers: {
              press: {
                time: 700,
                threshold: 10
              }
            }
          }}>
          <div ref="content" className={s.content + " " + (s.anim )}>
            {this.props.children}
          </div>
        </Hammer>

      </div>
    )
  }

}

export default SwipeOut



