import {} from '../constants';
import {dispatchAction, showDialog as _showDialog} from './actions'
import {ActionType, StateActionType} from '../utils/actions'
import loadingProgress from '../utils/loadingProgress'
import {callApi} from '../core/api'
import {navigate} from './routeActions'

export const ModuleName = 'Email';
export const GetEmailList =new StateActionType('EmailList', ModuleName);
export const DeleteEmailList =new StateActionType('DeleteEmailList', ModuleName);
export const UpdateEmail=new StateActionType('UpdateEmail',ModuleName);
export const GetCreditCardDetials=new StateActionType('GetCreditCardDetials',ModuleName);
export const EmailLoginIn=new StateActionType('EmailLoginIn',ModuleName);

/**
 * 
 */
export function getEmailList() {
  return (dispatch) => {
    dispatch(GetEmailList.newStartAction());
    loadingProgress.loading('');
    return callApi('/tally/boundEmailList')
      .then(({data})=> {
        dispatch(GetEmailList.newSucceedAction(data));
        loadingProgress.hide();
      }).catch(({message})=>{
        dispatch(GetEmailList.newErrorAction({error:message}));
        loadingProgress.info('');
      });
  }
}
/**
 * 
 */
export function deleteEmailList(email,emailId) {
  return (dispatch) => {
    loadingProgress.loading('');
    let params= {email:email,emailId:emailId };

    dispatch(DeleteEmailList.newStartAction());
    callApi('/tally/deleteEmailList', params)
      .then(({data})=>{
        const d = Object.assign(data.resultData, params);
        dispatch(DeleteEmailList.newSucceedAction(d))
    }).catch(({message})=>{
        dispatch(DeleteEmailList.newErrorAction({error:message}))
      loadingProgress.info('',1);
    })
  }
}
/**
 * 
 */
export function getCreditCardDetials(year,month,emailAddress,tailNumber,bankId) {
  return(dispatch) =>{
    loadingProgress.loading('');
    let params ={bankId:bankId,tailNumber:tailNumber,emailAddress:emailAddress,year:year,month:month};
    dispatch(GetCreditCardDetials.newStartAction(params));
    return callApi('/tally/creditCardBillDetail',params)
      .then(({data})=>{
        const d = Object.assign({"creditCardDetail":data.resultData,"year":params.year,"month":params.month});
        dispatch(GetCreditCardDetials.newSucceedAction(d))
        loadingProgress.info('',1);
      }).catch(({message})=>{
        dispatch(GetCreditCardDetials.newErrorAction({error:message}))
        loadingProgress.info(message,1);
      })
  }
}
/**
 * 
 */
export function upDateEmail(email) {
  return (dispatch) => {
    loadingProgress.loading('');
    let params = {email:email};
    dispatch(UpdateEmail.newStartAction(params));
    return callApi('/tally/mailImport',params)
      .then(({data})=>{
        const d= Object.assign(data.resultData,params);
        dispatch(UpdateEmail.newSucceedAction(d))
        loadingProgress.info('',1);
      }).catch(({message})=>{
        dispatch(UpdateEmail.newErrorAction({error:message}))
        loadingProgress.info('',1);
      })
  }
}
/**
 * 
 */
export function emailLoginIn(emailAddress,emailPassWord) {
  return(dispatch) =>{
    loadingProgress.loading('',1);
    let params ={emailAddress:emailAddress,emailPassWord:emailPassWord};
    dispatch(EmailLoginIn.newStartAction(params));
    return callApi('/tally/mailVerifyPwd',params)
      .then(({data}) =>{
        loadingProgress.hide();
        blockLoginIn(dispatch,data.resultData,emailAddress,emailPassWord);
      }).catch(({message})=>{
        dispatch(EmailLoginIn.newErrorAction({error:message}))
        loadingProgress.info('',1);
      })
  }
}
/**
 * 
 */
export function importEmailVerify(emailAddress,emailPassWord,nextStep,code,emailAuth) {
  return(dispatch) =>{
    let params = {emailAddress:emailAddress,emailPassWord:emailPassWord,emailAuth:emailAuth};
    let url;
    if (nextStep == 1) {
      url = '/tally/mailVerifyImg';
      params.imgCode=code;
    } else if (nextStep == 2) {
      url = '/tally/mailVerifySms';
      params.messageCode=code;
    } else if (nextStep == 3) {
      url = '/tally/mailVerifyIndie';
      params.password=code;
    }
    loadingProgress.loading('',1);
    dispatch(EmailLoginIn.newStartAction(params));
    return callApi(url,params)
      .then(({data}) =>{
        blockLoginIn(dispatch,data.resultData,emailAddress,emailPassWord);
        loadingProgress.hide();
      }).catch(({message})=>{
        dispatch(EmailLoginIn.newErrorAction({error:message}))
        loadingProgress.info('',1);
      })
  }
}

//,
function blockLoginIn(dispatch,resultData,emailAddress,emailPassWord){
  if(resultData.nextStep ==-1){
    dispatch(emailLoginIn(emailAddress,emailPassWord));
  }else{
    if(resultData.nextStep == 4){
      dispatch(navigate(`/emailImport/emailBill`))
    }
    dispatch(EmailLoginIn.newSucceedAction(resultData));
  }
}



