import React, {Component, PropTypes} from "react";
import {STATIC_IMG_URL} from '../../config'
import s from "./TopTab.css";
class TopTab extends Component {
  constructor() {
    super();
    this.state = {data:{selectNum:''}};
  }
  componentWillMount() {
    this.state.data.selectNum = this.props.option.selectNum;
    this.setState({
      data: {selectNum: this.state.data.selectNum}
    });
  }
  componentWillReceiveProps(nextProps) {
    this.props = nextProps;
    this.state.data.selectNum = this.props.option.selectNum;
    this.setState({
      data: {selectNum: this.props.option.selectNum}
    });
  }

  testSelectedNum(num) {
    if (this.props.selected) {
      this.props.selected(num);
    }
  }

  render() {
    var listItemNodes = this.props.option.nameArray.map(function (item, i) {
      return (
        <ListItem
          key={"tab-item-" + i}
          index={i}
          selectNum={this.state.data.selectNum}
          allNum={this.props.option.nameArray.length}
          selected={this.testSelectedNum.bind(this)}
        >
          {item.name}
        </ListItem>
      )
    }.bind(this));
    return (
      <div className={s.tabContainer}>
        <div className={s.tabList}>
          {listItemNodes}
        </div>
        <ActiveLine
          selectNum={this.state.data.selectNum}
          allNum={this.props.option.nameArray.length}
        />
      </div>
    )
  }
}
class ListItem extends Component {
  handleTouchTap() {
    if (this.props.index == this.props.selectNum) {
      return;
    } else {
      if (this.props.selected) {
        this.props.selected(this.props.index);
      }
    }
  }

  render() {
    this.tabSize = this.props.allNum;
    this.singleTabWidth = (100 / this.tabSize).toFixed(8) + "%";
    var style = {
      "width": this.singleTabWidth
    }
    //choose_topTab_noSelected.png
    return (
      <div onClick={this.handleTouchTap.bind(this)}
          className={this.props.index == this.props.selectNum ? s.tabItemActive : s.tabItem} ref="itemTap"
          id={this.props.index} style={style}>
        <img className={s.noSelectedImage} src={STATIC_IMG_URL + "/choose_topTab_noSelected.png"}/>
        {this.props.children}
      </div>
    )
  }
}
class ActiveLine extends Component {
  render() {
    this.tabSize = this.props.allNum;
    this.singleTabWidth = (100 / this.tabSize).toFixed(8) + "%";
//choose_topTab_selected.png
    var style = {
      "transform": "translateX(" + 100 * this.props.selectNum + "%)",
      "WebkitTransform": "translateX(" + 100 * this.props.selectNum + "%)",
      "width": this.singleTabWidth
    }
    return (
      <div className={s.activeUnderline} ref="activeUnderline" style={style}>
        <img className={s.selectedImage} src={STATIC_IMG_URL + "/choose_topTab_selected.png"}/>
      </div>
    )
  }
}
export default TopTab;



