import React from 'react';
import ScenicDetail from './ScenicDetail';
import ChooseLines from './ChooseLines';
import Load from './Load';

export default {

  path: '/huashan',
  children:[
    {
      path: '/ScenicDetail/:id',
      action({context, params, query}) {
        return <ScenicDetail id={params.id}/>;
      }
    },
    {
      path:'/ChooseLines',
      action() {
        return <ChooseLines />;
      }
    },
    {
      path:'/Load',
      action() {
        return <Load />;
      }
    },
    {
      path: '*',
      action() {
        return <ChooseLines />;
      }
    },
  ],
  action({next}){
    return next();
  }
};



