import { SHOW_DIALOG } from '../constants';



export function dispatchAction(action) {
  return (dispatch) => {
    dispatch(action)
  }
}


export function showDialog(actionName, dialog) {

  return (dispatch) => {
    dispatch({
      type:actionName,
      dialog:dialog
    })
  };
}



