import querystring from 'query-string';
import {MOCK} from '../../config'
import {mockManager} from '../../mock'
import {API_PATH} from '../../config'

class ApiError {

  constructor(request, response, code, message) {
    this.request = request;
    this.response = response;
    this.code = code;
    this.message = message;
  }

  toString() {
    return '(' + this.code + ', ' + this.message + ')';
  }

}

/**
 * ,
 * @param url
 * @param method
 * @param params
 * @param responseBodyType
 * @returns {*}
 */
function _callApi(url, params, {method = "GET", responseBodyType = 'json'} = {}) {
  //TODO auth, ua, clientVersion

  let body = undefined;
  if ('POST' == method || 'PUT' == method) {
    body = JSON.stringify(params);
  } else if ('GET' == method || 'delete' == method) {
    if (params) {
      url += (url.indexOf('?') > 0 ? '' : '?') + querystring.stringify(params);
    }
  }

  console.log('request:' + url + ', body:' + JSON.stringify(body));
  let request = null;
  let response = null;
  let responseBody = null;

  let headers = {
    'Accept': 'application/json',
    'Content-Type': 'application/json',
  };
  let requestOptions = {
    method: method,
    headers: headers,
    body: body
  };
  request = new Request(url, requestOptions);
  let returnPromise = fetch(url, requestOptions);
  returnPromise.then((resp) => {
    response = resp;
    // ,
    // resp.clone().text().then((text) => {
    //   console.log("response:" + url + ', status:' + resp.status + ', data:' + text);
    // });
    return resp;
  });


  returnPromise.catch((error) => {
    console.log("response:" + url + ', error:' + error);
    throw error;
  });

  if (responseBodyType) {
    if ('json' == responseBodyType) {
      returnPromise = returnPromise.then((resp) => {
        return resp.json();
      }).then((jsonBody) => {
        responseBody = jsonBody;
      });
    } else if ('text' == responseBodyType) {
      returnPromise = returnPromise.then((resp) => {
        return resp.text();
      }).then((textBody) => {
        responseBody = textBody;
      });
    }
  }

  returnPromise = returnPromise.then(() => {
    return {request, response, data: responseBody}
  });

  return returnPromise;
}

/**
 * ,
 * @param urlPath
 * @param params
 * @param options {method="POST", responseBodyType = 'json'}
 * @returns {Promise.<TResult>} {requst, respone,data}error{request,response, code, message}
 */
export function callApi(urlPath, params, {method = "POST", responseBodyType = 'json'} = {}) {

  let returnRequest, returnResponse, returnData, returnError;

  //
  params = Object.assign(
    {
      version: 2.0,
      deviceId: null,
      clientName: 'h5',
      auth: null,
      customerId: '111111',
      macAddress: "02:e4:37:ac:d3:7b ",
    },
    (params ? params : {}));

  let returnPromise = null;

  //mock 
  if (MOCK && mockManager) {
    let mockData = mockManager.getMockData(urlPath);
    if (mockData) {
      returnRequest = mockData.request;
      returnResponse = mockData.response;
      returnData = mockData.responseData;
      returnError = mockData.error;

      returnPromise = new Promise((resolve, reject) => {
        setTimeout(()=> { //
          if (returnError) {
            reject(returnError)
          } else {
            resolve(returnData);
          }
        }, mockData.delay || 0);
      }).then(() => {
        return {request: null, response: null, data: returnData}
      });
      return returnPromise;
    }
  }

  returnPromise = _callApi(apiUrl(urlPath), params, {
    method,
    responseBodyType,
  }).then(({request, response, data}) => {
    returnRequest = request;
    returnResponse = response;
    returnData = data;

    //
    if (responseBodyType == 'json') {
      // if (data.resultCode != 0) {
      //   throw new ApiError(
      //     request,
      //     response,
      //     (data.resultCode ? data.resultCode : -400),
      //     (data.resultMsg ? data.resultMsg : ','));
      // }
    }

  }).catch((error) => {
    //
    console.log('call api error:' + error);

    if (error instanceof ApiError) {
      throw error;
    }

    throw new ApiError(
      returnRequest,
      returnResponse,
      -1,
      ',',
    )
  }).then(() => {
    return {
      request: returnRequest,
      response: returnResponse,
      data: returnData,
    }
  });

  return returnPromise;
}

/**
 * apiurl
 * @param path host
 * @returns {string}
 */
export function apiUrl(path) {
  return API_PATH + path;
}




