import React, {Component, PropTypes} from "react";
import {Modal} from "antd-mobile";
import {connect} from "react-redux";
import * as _emailAction from "../../actions/emailAction";
import {navigate} from "../../actions/routeActions";
import {bindActionCreators} from "redux";
import s from "./EmailLogin.css"
import flex from "./EmailAllStyle.css"
import {STATIC_IMG_URL} from '../../config'
import loadingProgress from '../../utils/loadingProgress'

class EmailLogin extends Component {
  componentWillUpdate() {
  }
  handleCommentSubmit(comment){
    this.state.userName=comment.userName;
    this.state.passWord=comment.passWord;
    this.setState({
      userName:this.state.userName,
      passWord:this.state.passWord
    });
  }
  popChangeCode(comment){
    this.state.code=comment.code;
    this.setState({
      code:this.state.code
    })
  }
  //
  isEmail(str){
    var reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
    return reg.test(str);
  }
  submitAllData(){
    if(!this.state.userName || !this.state.passWord){
      return;
    }else{
      let isEmail = this.isEmail(this.state.userName);
      if(isEmail){
        // this.props.navigate('/emailImport/emailBill');
        this.props.emailAction.emailLoginIn(this.state.userName,this.state.passWord);
      }else{
        loadingProgress.info('',1);
      }
    }
  }
  constructor() {
    super();
    this.state = {userName: '', passWord: '',code:''}
  }

  render(){
    this.windowHeight=window.innerHeight;
    var leftStyle={
      "height":this.windowHeight+"px",
    }
    return (
      <div className={s.allView} style={leftStyle}>
        <TopSafety/>
        <div className={s.middleGaryView}/>
        <MiddleInput data={this.state} onCommentSubmit={this.handleCommentSubmit.bind(this)}/>
        <div className={s.agreeProtocol + ' ' + flex.flexContainer + ' ' + flex.flexContainerNoWrap}>
          <div style={{'color': '#999999'}}></div>
          
        </div>
        <BottomView data={this.state} onClick={this.submitAllData.bind(this)}/>

        <Modal
          closable={false}
          transparent
          visible={this.props.nextStep ==1}
          footer={[{ text: '', onPress: () => {this.props.emailAction.importEmailVerify(this.state.userName,this.state.passWord,this.props.nextStep,this.state.code,this.props.emailAuth)} }]}
        >
          <div className="s.popView" onChange={this.popChangeCode.bind(this)}>
            <img className={s.popImage} src={STATIC_IMG_URL + "/email_pop_image.jpg"}/>
            <div className={s.poptext}></div>
            <input className={s.popInput} type="text" placeholder="" ref="code"/>
          </div>
        </Modal>
        <Modal
          closable={false}
          transparent
          visible={this.props.nextStep ==2}
          footer={[{ text: '', onPress: () => {this.props.emailAction.importEmailVerify(this.state.userName,this.state.passWord,this.props.nextStep,this.state.code,this.props.emailAuth)} }]}
        >
          <div className="s.popView" onChange={this.popChangeCode.bind(this)}>
            <div className={s.poptitle}></div>
            <div className={s.poptext}></div>
            <input className={s.popInput} type="text" placeholder="" ref="code"/>
          </div>
        </Modal>
        <Modal
          closable={false}
          transparent
          visible={this.props.nextStep ==3}
          footer={[{ text: '', onPress: () => {this.props.emailAction.importEmailVerify(this.state.userName,this.state.passWord,this.props.nextStep,this.state.code,this.props.emailAuth)} }]}
        >
          <div className="s.popView" onChange={this.popChangeCode.bind(this)}>
            <div className={s.poptitle}></div>
            <div className={s.poptext}></div>
            <input className={s.popInput} type="text" placeholder="" ref="code"/>
          </div>
        </Modal>
      </div>
  )
  }
}

class TopSafety extends Component {
  render(){
    return(
      <div
        className={s.topSafety + ' ' + flex.flexContainer + ' ' + flex.flexContainerNoWrap + ' ' + flex.vCenter + ' ' + flex.hBoxOrient}>
        <div className={s.left}>
          <img className={s.leftImage} src={STATIC_IMG_URL + "/emailSafety.png"}/>
        </div>
        <div
          className={s.right + ' ' + flex.flexContainer + ' ' + flex.flexContainerNoWrap + ' ' + flex.vCenter + ' ' + flex.vBoxOrient + ' ' + flex.vStretch + ' ' + flex.hJustify}>
          <div className={s.rightTop}></div>
          <div className={s.rightBottom}>,</div>
        </div>
      </div>
  )
  }
}

class MiddleInput extends Component{
  handleSubmit(e) {
    e.preventDefault()
    var userName = this.refs.userName.value;
    var passWord = this.refs.passWord.value;
    this.props.onCommentSubmit({userName: userName, passWord: passWord});
    return;
  }
  constructor(){
    super();
  }
  render() {
    this.windowWidth=window.innerWidth;
    var inputStyle={
      "width":(this.windowWidth-180)+"px",
    }
    return (
      <div className={s.middleInputView} onChange={this.handleSubmit.bind(this)}>
        <div
          className={s.middleUserName + ' ' + flex.flexContainer + ' ' + flex.flexContainerNoWrap + ' ' + flex.vCenter}>
          
          <input className={s.middleInput} style={inputStyle} type="text" placeholder="" ref="userName"/>
        </div>
        <div
          className={s.middlePassWord + ' ' + flex.flexContainer + ' ' + flex.flexContainerNoWrap + ' ' + flex.vCenter}>
          
          <input className={s.middleInput} style={inputStyle} type="password" placeholder="" ref="passWord"/>
        </div>
      </div>
  )
  }
}
class BottomView extends Component{
  clickBtn(){
    this.props.onClick();
  }
  render(){
    if(!this.props.data.userName || !this.props.data.passWord) {
      var btnStyle={
        'backgroundColor':'#dddddd',
        'color':'#999999'
      }
    }else{
      var btnStyle={
        'backgroundColor':'#f15353',
        'color':'#ffffff'
      }
    }
    return(
      <div>
        <div
          className={s.bottomBtn + ' ' + flex.flexContainer + ' ' + flex.flexContainerNoWrap + ' ' + flex.vCenter + ' ' + flex.hCenter}
          style={btnStyle}
          onClick={this.clickBtn.bind(this)}>
          
        </div>
        <div className={s.btnBottomLabel}>
          : 
        </div>
      </div>
  )
  }
}

const ConnectedComp = connect(
  store => ({
    nextStep: store.email.nextStep,
    message: store.email.message,
    emailAuth:store.email.emailAuth,
  }),
  dispatch => ({
    emailAction: bindActionCreators(_emailAction, dispatch),
    navigate: bindActionCreators(navigate, dispatch),
  })
)(EmailLogin);

export default ConnectedComp;



