import {ActionHandlerManager, StateActionHandler, ActionHandler} from '../utils/actions'
import {GetEmailList,DeleteEmailList,UpdateEmail,GetCreditCardDetials,EmailLoginIn} from '../actions/emailAction'
import {ShowDialogHandler} from './actionHandlers'
import loadingProgress from '../utils/loadingProgress'

const handlerManager = new ActionHandlerManager();


const initState = {
  emailList:null,
  creditCardDetail:null,
  year: new Date().getFullYear(),
  month: new Date().getMonth()+1,
  nextStep:-2,
  message:null,
  emailAuth:null,
};
export default function emailReducer(state = initState, action) {
  return handlerManager.dispatch(state, action);
}
/**
 * 
 */
handlerManager.addHandler(new StateActionHandler(EmailLoginIn,{
  onStart(state, action){
    let ns =Object.assign({},state);
    ns.nextStep=-2;
    return ns;
  },
  onSucceed(state, action){
    let ns =Object.assign({},state,action);
    return ns;
  }
}))

/**
 * 
 */
handlerManager.addHandler(new StateActionHandler(GetEmailList,{
  onStart(state, action){
    let ns =Object.assign({},state);
    return ns;
  },
  onSucceed(state, action){
    let ns =Object.assign({},state);
    ns.emailList = action.emailList;
    ns.emailList = Array.from(ns.emailList);
    return ns;
  },
  onError(state, action){
    let ns = Object.assign({}, state);
    return ns;
  }
}));

/**
 * 
 */
handlerManager.addHandler(new StateActionHandler(DeleteEmailList,{
  onStart(state, action){
    let ns =Object.assign({},state);
    return ns;
  },
  onSucceed(state, action){
    if(action.success){
      loadingProgress.success('',1);
      let ns =Object.assign({},state);
      let index;
      for (let i = 0; i < ns.emailList.length; i++) {
        let data = ns.emailList[i];
        if (data.email == action.email && data.emailId == action.emailId) {
          index = i;
          break;
        }
      }
      ns.emailList.splice(index, 1);
      ns.emailList = Array.from(ns.emailList);
      return ns;
    }else {
      loadingProgress.success('',1);
    }
  },
  onError(state, action){
    let ns = Object.assign({}, state);
    return ns;
  }
}))

/**
 * 
 */
handlerManager.addHandler(new StateActionHandler(UpdateEmail,{
  onStart(state, action){
    let ns =Object.assign({},state,{isUpdate:true});
    let index;
    for (let i = 0; i < ns.emailList.length; i++) {
      let data = ns.emailList[i];
      if (data.email == action.email) {
        index = i;
        break;
      }
    }

    ns.emailList[index].isUpdate=true;
    ns.emailList = Array.from(ns.emailList);
    return ns;
  },
  onSucceed(state, action){
    let ns =Object.assign({},state,{isUpdate:false});
    let index;
    for (let i = 0; i < ns.emailList.length; i++) {
      let data = ns.emailList[i];
      if (data.email == action.email) {
        index = i;
        break;
      }
    }
    ns.emailList[index].isUpdate=false;
    ns.emailList = Array.from(ns.emailList);
    return ns;
  },
  onError(state, action){
    loadingProgress.success('',1);
    let ns = Object.assign({}, state,{isUpdate:false});
    let index;
    for (let i = 0; i < ns.emailList.length; i++) {
      let data = ns.emailList[i];
      if (data.email == action.email) {
        index = i;
        break;
      }
    }
    ns.emailList[index].isUpdate=false;
    ns.emailList = Array.from(ns.emailList);
    return ns;
  }
}))

/**
 * 
 */
handlerManager.addHandler(new StateActionHandler(GetCreditCardDetials,{
  onStart(state,action){
    let ns =Object.assign({},state,{"year":action.year,"month":action.month});
    return ns;
  },
  onSucceed(state, action){
    let ns =Object.assign({},state,action);
    return ns;
  },
  onError(state, action){
    let ns = Object.assign({}, state);
    return ns;
  }
}))



