import {ActionHandlerManager, StateActionHandler, ActionHandler} from '../utils/actions'
import {GetTypeList} from '../actions/typesActions'

const handlerManager = new ActionHandlerManager();


export default function typesReducer(state = initState, action) {
  return handlerManager.dispatch(state, action);
}


/**
 * 
 */
handlerManager.addHandler(new StateActionHandler(GetTypeList, {

  onSucceed(state, action){
    if(!action.types || action.types.length == 0){
      return state;
    }
    let ns = Object.assign({}, state);
    ns.types = Array.from(action.types);
    return ns;
  },

}));


function getTypeById(id) {
  if(!this.types || this.types.length == 0){
    return null;
  }

  return this.types.find((o) => o.typeOpcodes==id);
}

const initState = {
  types:[],
  categories:[],
  getTypeById:getTypeById
};


