import React, {Component, PropTypes} from "react";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import s from "./TradeDetail.less";
import * as _tradesActions from "../../actions/tradesActions";
// import * as _typeAction from "../../actions/typesActions";
import {navigate} from "../../actions/routeActions";
import {STATIC_IMG_URL} from '../../config'
import {IOType, TallyChannels, TypeDefaultImage} from '../../constants'
import {ListLabelValueItem} from '../../components/ListItem'
import RelativeModule from '../../components/RelativeModule'
import {formatToyyyyMMdd} from '../../utils/timeUtils'


class TradeDetail extends Component {

  static contextTypes = {
    setTitle: PropTypes.func.isRequired,
  };

  componentWillMount() {
    this.props.tradesActions.getTradeDetail(this.props.tradeId);
  }

  render() {
    this.context.setTitle('');
    let {trade, type} = this.props;
    //TODO 
    return (
      <div className={s.root}>
        <div className={s.detailContent}>
          <div className={s.listItem2} onClick={this.props.tradesActions.editTrade.bind(null, trade)}>
            <div className={s.content}>
              <img className={s.icon} src={type ? type.iconDisplay : TypeDefaultImage}/>
              <div className={s.left}>
                <div className={s.l1}>{type ? type.typeDesc : null}</div>
                <div className={s.l2}>{TallyChannels.getNameById(trade.tallyChannel)}</div>
              </div>
              <div className={s.right}>
                <div className={s.r1} style={{color: '#333'}}>{trade.amount ? trade.amount.toFixed(2) : ''}</div>
                {/*<div className={s.r2}>0.00</div>*/}
              </div>
              <img className={s.arrow} src={STATIC_IMG_URL + "/common_ic_arrow_right.png"}/>
            </div>
            <hr className={s.divider}/>
          </div>
          <div className={s.details}>
            <ListLabelValueItem label="" value={trade.ioType == IOType.INCOME ? '' : ''}/>
            <ListLabelValueItem label="" value={formatToyyyyMMdd(trade.time)}/>
            {trade.tallyChannel == TallyChannels.JD_ORDER.id
              ? <div className={s.jdOrderRemark}>
              {trade.photoUrl && trade.photoUrl.length > 0 ? <img className={s.photo} src={trade.photoUrl[0]}/> : null}
              <span className={s.remark}>{trade.remark}</span>
            </div>
              : <ListLabelValueItem label="" value={trade.remark}/>}
          </div>
        </div>
        <RelativeModule className={s.relateModule} module={type ? type.SysTypeModule: null} />
      </div>
    )
  }
}


const ConnectedComp = connect(
  (store, ownProps) => {
    let trade = store.trades.details[ownProps.tradeId] || {tallyId: ownProps.tradeId};
    let type = null;
    if (trade.typeOpcodes !== undefined && trade.typeOpcodes !== null) {
      type = store.types.types.find((o)=> o.typeOpcodes == trade.typeOpcodes);
    }
    return {
      trade,
      type,
    }
  },
  dispatch =>({
    tradesActions: bindActionCreators(_tradesActions, dispatch),
    navigate: bindActionCreators(navigate, dispatch),
  })
)(TradeDetail);

export default ConnectedComp;


