import 'babel-polyfill';
import 'whatwg-fetch';
import 'antd-mobile/lib/style'

import React from 'react';
import ReactDOM from 'react-dom';
import FastClick from 'fastclick';
import {Provider} from 'react-redux';
import App from './components/App'

import store from './core/store';
// import router from './core/router';
import history from './core/history';
import UniversalRouter from 'universal-router';
import routes from './routes'

// let routes = require('./routes.json'); // Loaded with utils/routes-loader.js

const container = document.getElementById('appContainer');

const context = {
  store: store,
  setTitle: value => (document.title = value),
};

// function renderComponent(component) {
//   ReactDOM.render(<App  context={context}>{component}</App>, container);
// }

function renderComponent(container, state, component) {
  return new Promise((resolve, reject) => {
    try {
      ReactDOM.render(
        component,
        container,
        null
      );
    } catch (err) {
      reject(err);
      console.error(err);
      throw  err;
    }
  });
}

// Find and render a web page matching the current URL path,
// if such page is not found then render an error page (see routes.json, core/router.js)
function render(location) {
  UniversalRouter.resolve(routes, {
    path: location.pathname,
    query: location.query,
    state: location.state,
    context,
    render: renderComponent.bind(undefined, container, location.state), // eslint-disable-line react/jsx-no-bind, max-len
  }).catch(error => {
    return UniversalRouter.resolve(routes, {...location, error}).then(renderComponent);
  });
}

// Handle client-side navigation by using HTML5 History API
// For more information visit https://github.com/ReactJSTraining/history/tree/master/docs#readme
history.listen(render);
document.body.onload = () => {
  render(history.getCurrentLocation());
};


// Eliminates the 300ms delay between a physical tap
// and the firing of a click event on mobile browsers
// https://github.com/ftlabs/fastclick
FastClick.attach(document.body);

